* {
  box-sizing: border-box;
}

body,
ul {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
  list-style: none;
}

.stars__star {
  height: 16px;
  width: 16px;

  background: no-repeat center url(./images/star-active.svg);
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(n),
.stars--1 .stars__star:nth-child(n +2),
.stars--2 .stars__star:nth-child(n +3),
.stars--3 .stars__star:nth-child(n +4),
.stars--4 .stars__star:nth-child(n +5),
.stars--5 .stars__star:nth-child(n +6) {
  background-image: url(./images/star.svg);
}
